  },
  mounted() {
    $('#myTitle').html(this.message.title);
  },
  methods: {
    goWay(num) {
      if (num == 1) {
        this.$router.push({ path: '/fastpay' })
      } else if (num == 2) {
        this.$router.push({ path: '/accountBind' })
      }
    }
