 <template>
  <div class="water-query">
    <!-- <page-header>
      <header-link :left="true" :edge="true" v-back-link>
        <icon icon="back"></icon>返回
      </header-link>
      <header-title>未绑定用户</header-title>
    </page-header> -->
    <page-content>
      <div class="content-padded">
        <!-- <myHeader :isShow="isShow" :title='title'></myHeader> -->
        <headerThree :message = 'message'></headerThree>
        <!-- <div class="row add_box">
          <div class="col-100">
            <div class="midle-bar">
              <b></b>
              <router-link :to="{ path: 'accountBind' }">
                <img src="../../../static/img/plus.png" alt="">
              </router-link>
            </div>
          </div>
        </div> -->

        <div class="kr-body">
          <div class="kr-containt">
            <div class="kr-top">
              <img src="../../../static/img/youzhi.png" alt="">
            </div>
            <div class="kr-middle">
             <p>Hi,</p>
             <p>您还没有绑定水表，绑定后将享受到</p>
             <p>更便捷更优质的用水服务</p>
            </div>
            <div class="kr-buttom">
              <router-link :to="{ path: 'accountBind' }"> 
                <m-button size="big" type="danger">
                  立即体验
                </m-button>
              </router-link> 
            </div>
          </div>
        </div>
        
      </div>
    </page-content>
  </div>
</template>

<script>
import myHeader from '@/comments/myHeader'
import headerThree from '@/comments/headerThree';
export default {
  components:{
    myHeader,headerThree
  },
  data() {
    return {
      message:{
        title:"请绑定用户",
        span1:"尊敬的"+localStorage.getItem('nickname'),
        span2:"请绑定您的用户",
        isShow: '',
      },
      isShow:1,
      title:"请绑定用户",
      // title:'粤海水务',
    }
  },
  mounted() {
    
  },
  methods: {
    
  },
  watch: {

  }
}
</script>

<style scoped lang="less">
.water-query {
  .content-padded{
    height: 100%;
    margin: 0;
    .kr-body{
      padding:10% 5%;
      height: 70%;
      // margin-top:10%;
      .kr-containt{
        box-shadow: 0px 1px 9px -1px #888;
        border-radius: 10px 10px 0px 0px;
        .kr-top{
          padding: 16px 10px 0 10px;
          text-align: center;
          img{
            width: 100px;
          }
        }
        .kr-middle{
          padding:10px 0px 10px 5%;
          p{
            font-size: 16px;
          }
        }
        .kr-buttom{
          
          .button{
            height: 60px;
            line-height: 60px;
            border-radius: 0;
            // font-family: PingFang_Medium;
            font-size: 18px;
          }
          .button-danger {
            background: #ed702d;
          }
          .button-success {
            background: #49cc7c;
          }
        }
      }
    }
    .add_box {
      position: absolute;
      width: 100%;
      top: 26%;
      height: 10%;
      padding: 0px 5px;
    }   
  }
  .row{
    margin-left: 0;
  }
  .col-100 {
    .midle-bar {
      padding: 0 0 0 15px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      justify-content: space-between;
      b {
        font-size: 20px;
        line-height: 40px;
      }
    }
    .list {
      .item-media {
        margin-right: 10px;
        width: 20%;
        .pay-button {
          font-size: 18px;
          background: blue;
          height: 100%;
          width: 100%;
        }
      }
      .item-content {
        .item-title {
          height: 1.6rem;
          line-height: 1.6rem;
          img {
            position: relative;
            top: 0.2rem;
          }
        }
      }
    }
  }
}
</style>


